import React from "react";
import { PropTypes } from "mobx-react";

const Cities = ({ cities }) => {
  return (
    <div className="visit-cities">
      <div className="visit-cities-icon">
        <svg fill="#b8860b" height="24" viewBox="0 0 24 24" width="24" xmlns="http://www.w3.org/2000/svg">
          <path d="M12 2C8.13 2 5 5.13 5 9c0 5.25 7 13 7 13s7-7.75 7-13c0-3.87-3.13-7-7-7zm0 9.5c-1.38 0-2.5-1.12-2.5-2.5s1.12-2.5 2.5-2.5 2.5 1.12 2.5 2.5-1.12 2.5-2.5 2.5z"/>
          <path d="M0 0h24v24H0z" fill="none"/>
        </svg>
      </div>
      <div className="visit-city-names">
        {cities.map((city, index) =>
          <span className="visit-city" key={index}>
            {city}
          </span>
        )}
      </div>
    </div>
  );
};

export default Cities;

Cities.propTypes = {
  cities: PropTypes.observableArray.isRequired
}


